import { NextResponse } from "next/server"
import { getUser, getAuthUser } from "@/lib/auth-utils"

export async function GET() {
  try {
    const user = await getUser()
    if (user) {
      return NextResponse.json({
        id: user.id,
        email: user.email,
        name: (user as any).name ?? undefined,
        role: user.role,
      })
    }

    // Fallback: если пользователь в БД не найден, но токен валиден — вернуть данные из токена
    const payload = await getAuthUser()
    if (payload) {
      return NextResponse.json({ id: payload.userId, email: payload.email, role: payload.role })
    }

    return NextResponse.json({ error: "Unauthorized" }, { status: 401 })
  } catch (error) {
    console.error("Error fetching user:", error)
    return NextResponse.json({ error: "Internal server error" }, { status: 500 })
  }
}
